'use strict';

(function () {
  let submitButton = document.querySelector('.questions__button');
  let telField = document.querySelector('.questions__field--telephone');


  let validateField = function (field, validity) {
    if (validity === 'valid') {
      if (document.querySelector('[data-type="message"]')) {
        document.querySelector('[data-type="message"]').remove();
        field.classList.remove('questions__field--invalid');
      }
    } else if (validity === 'invalid') {
      let message = document.createElement('div');
      if (!document.querySelector('[data-type="message"]')) {
        message.textContent = 'Данные не верны';
        message.style = 'position: absolute; z-index: 2; top: -22px; color: red';
        message.setAttribute('data-type', 'message');
        field.parentNode.prepend(message);
        field.classList.add('questions__field--invalid');
      }
    }
  }
  let validateTel = function (evt) {
    evt.preventDefault();
    let notNumber = /\D/;
    let notSpace = /\S/;
    let wordSpaceString = telField.value.replace(/\d/g, '').replace(/-/g, '');

    if (notNumber.test(telField.value)) {
      if (notSpace.test(wordSpaceString)) {
        validateField(telField, 'invalid');
      } else {
        validateField(telField, 'valid');
      }
    } else {
      validateField(telField, 'valid');
    }
  }

  submitButton.addEventListener('click', validateTel);

})();
